.wcs_featured{
    flex: 2;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0,0,0,0.47);
    box-shadow: 2px 4px 10px 1px rgba(201,201,201,0.47);
    padding: 10px;
    
    .wcs_top{
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: gray;

        .wcs_title{
            font-size: 16px;
            font-weight: 500;
        }
    }

    //Circular chart 
    .wcs_bottom{
        padding: 20px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 15px;
        //size
        .wcs_featuredChart{
            width: 100px;
            height: 100px;
        }
        .wcs_title{
            font-weight: 500;
            color: gray;
            margin-top: 6px;
            margin-bottom: 0px;
        }
        .wcs_amount{
            font-size: 30px;
            margin: 0px;
        }
        .wcs_desc{
            font-weight: 300;
            font-size: 12px;
            color: gray;
        }

        .wcs_summary{
            width: 100%;
            display: flex;
            align-items:center;
            justify-content: space-between ;

            .wcs_item{
                text-align: center;
                .itemTitle{
                    font-size: 14px;
                    color: gray;
                }
                .itemResult{
                    display: flex;
                    align-items: center;
                    margin-top: 10px;
                    font-size: 14px;

                    &.positive{
                        color: green;
                    }
                    &.negative{
                        color: red;
                    }
                }
            }
        }
    }
}